import React, { useState, useContext } from "react";
import { Checkbox, Image, Drawer, Row, Col } from "antd";
import { contentContext } from "./Search";

const Gallery = () => {
    const { show, content } = useContext(contentContext);
    const [open, setOpen] = useState(false);
    const showDrawer = () => {
        setOpen(true);
        console.log(123);
    };
    const onClose = () => {
        setOpen(false);
    };
    const onChange = (checkedValues) => {
        console.log("checked = ", checkedValues);
    };
    return (
        <div
            style={{
                width: "100%",
                height: "100%",
                marginTop: "2rem",
                position: "relative",
            }}
        >
            <div style={{ width: "100%", display: "flex", justifyContent: "center" }}>
                {show ? (
                    <Checkbox.Group
                        style={{
                            width: "25%",
                            color: "white",
                            fontWeight: 500,
                            textAlign: "left",
                        }}
                        onChange={onChange}
                    >
                        <div>
                            <div>
                                <Checkbox value="Background">
                                    <span style={{ color: "white" }}>Background</span>
                                </Checkbox>
                            </div>
                            <div>
                                <Checkbox value="Latest">
                                    <span style={{ color: "white" }}>Latest</span>
                                </Checkbox>
                            </div>
                            <div>
                                <Checkbox value="Most Viewed">
                                    <span style={{ color: "white" }}>Most Viewed</span>
                                </Checkbox>
                            </div>
                        </div>
                    </Checkbox.Group>
                ) : null}
            </div>
            <Row
                className="w-100"
                style={{
                    overflowY: "scroll",
                    color: "white",
                    height: "70%",
                    padding: "3rem",
                }}
            >
                {content ? (
                    content.map((res, idx) => (
                        <Col key={idx} style={{ padding: "3rem" }}>
                            <img width={200} height={200} onClick={showDrawer} src={require("../assets/pics/example.jpeg")} alt="example" />
                        </Col>
                    ))
                ) : (
                    <Image
                        width={200}
                        height={200}
                        src="error"
                        fallback="data:image/png;base64,"
                    />
                )}
            </Row>
            <Drawer title="Image Name" placement="right" onClose={onClose} open={open}>
                <Image
                    width={200}
                    height={200}
                    src={require("../assets/pics/example.jpeg")}
                    fallback="data:image/png;base64,"
                />

                <p>1920 X 1200</p>
            </Drawer>
        </div>
    );
};

export default Gallery;
