import React, { useState, createContext, useCallback, useMemo } from "react";
import PropTypes from "prop-types";
import { BarsOutlined } from "@ant-design/icons";
import searchArt from "../common/function/searchArt";
import Gallery from "./Gallery";
import { useParams, useNavigate, useLocation } from "react-router-dom";
export const contentContext = createContext();

const Search = ({ userInput, setUserInput }) => {
    const [show, setShow] = useState(false);
    const navigate = useNavigate();
    const [content, setConstent] = useState([]);
    const searchContent = useCallback(() => {
        setConstent(searchArt(userInput));
    }, [userInput]);
    const values = useMemo(
        () => ({
            show: show,
            content: content,
        }),
        [show, content]
    );
    return (
        <div
            style={{
                width: "100%",
                height: "100%",
                paddingTop: "3rem",
            }}
        >
            <div className="w-100" style={{ display: "flex", flexDirection: "row", justifyContent: "center", alignItems: "center" }}>
                <div>
                    <input
                        className="search_input"
                        value={userInput}
                        onChange={(e) => {
                            setUserInput(e.target.value);
                        }}
                    />
                </div>
                <div>
                    <button
                        style={{ backgroundColor: "transparent", borderRadius: "10px", border: "2px solid white" }}
                        onClick={() => {
                            setShow((thumb) => !thumb);
                        }}
                    >
                        <BarsOutlined className="option_icon" />
                    </button>
                </div>

                <div>
                    <button className="search_btn" type="primary" onClick={searchContent} loading={false}>
                        Search
                    </button>
                    <button className="search_btn" type="primary" onClick={() => navigate(`/test/login/1`)} loading={false}>
                        to Test
                    </button>
                </div>
            </div>
            <contentContext.Provider value={values}>
                <Gallery />
            </contentContext.Provider>
        </div>
    );
};
Search.propTypes = {
    userInput: PropTypes.string,
    setUserInput: PropTypes.func,
};
Search.defaultProps = {
    userInput: "",
    setUserInput: null,
};
export default React.memo(Search);
